import React from "react";
import Chart from "react-apexcharts";

import Container from "react-bootstrap/Container";

function LineDifferentBetas(props) {

    console.log(props);

    let series = !!props?.series ? props.series :
        [
            { name: 'Компания A', data: [100, 1500, 1150, 3000] },
            { name: 'Компания Б', data: [1000, 900, 1460, 1300] },
            { name: 'Компания В', data: [1000, 1300, 1250, 1550] },
            { name: 'Индекс', data: [1000, 1400, 1200, 1600] }
        ];

    let defaultBrightColorsLineApexCharts = {
        chart: {
            type: 'line',
            toolbar: {
                show: false,
            }
        },
        title: {
            text: '',
            align: 'left',
            style: {
                // color: "gray",
                // fontSize: '1rem',
                fontFamily: 'Helvetica, Arial, sans-serif',
                fontWeight: 'normal',
                marginTop: '2rem'
                // cssClass: 'apexcharts-title',
            }
        },
        stroke: {
            //        width: 7,
            curve: 'smooth'
        },
        xaxis: {
            type: 'datetime',
            categories: !!props?.categories ? props.categories : [...new Array(5)].map((item, index) => { var d = new Date(); return d.setMonth(d.getMonth() - 14 + index) }),
            title: {
                text: '',
                style: {
                    color: "gray",
                    fontSize: '1rem',
                    fontFamily: 'Helvetica, Arial, sans-serif',
                    fontWeight: 200,
                    cssClass: 'apexcharts-xaxis-title',
                    margin: '1rem',
                    padding: '1rem'
                }
            },
            style: {
                color: "gray",
                fontSize: '1rem',
                fontFamily: 'Helvetica, Arial, sans-serif',
                fontWeight: 200,
                cssClass: 'apexcharts-yaxis-title',
                margin: '1rem'            }
        },
        tooltip: {
            x: {
              format: "dd MMM yyyy",
            },
          },
        markers: {
            size: 2,
            colors: ["#D3D3D3"],
            strokeColors: "#808080",
            strokeWidth: 2,
            hover: {
                size: 4,
            }
        },
        yaxis:
        {
            // min: 800,
            // max: 1800,
            title: {
                text: '',
                style: {
                    color: "gray",
                    fontSize: '1rem',
                    fontFamily: 'Helvetica, Arial, sans-serif',
                    fontWeight: 200,
                    cssClass: 'apexcharts-yaxis-title',
                }
            },
            tickAmount: 5,
            labels: {
                formatter: function (val) {
                    return parseFloat(val).toFixed(1)
                }
            }
        }
    }

    let options = (() => {
        if (!!props?.options) {
            let updatedOptions = {...defaultBrightColorsLineApexCharts};
            updatedOptions = {...updatedOptions, ...props.options};
            updatedOptions.xaxis.categories = props.categories;
            return updatedOptions;
        };
        return defaultBrightColorsLineApexCharts
    })();

    return <Container className="mt-3" >
        <Chart options={options} series={series} categories={props.categories}/>
    </Container>

}

export default LineDifferentBetas;
